import React, { Component } from 'react';
import JSONSchemaForm from 'react-jsonschema-form';

class App extends Component {
  constructor() {
    super();
    this.state = { 
      schema: {}, 
      justSaved: false 
    };
  }

  async componentDidMount() {
    const schemaResponse = await fetch('http://localhost:3000/projects/schema');  
    const schema = await schemaResponse.json();

    const availableReports = await fetch('http://localhost:3000/projects/report');
    const reports = await availableReports.json();

    this.setState((state, props) => ({ schema, reports }))
  }

  async submit (event) {
    console.log(event)
    const result = await fetch('http://localhost:3000/projects/report', { 
        method: 'POST', 
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ 
          schema: this.state.schema, 
          data: event.formData
        })
    });
    if ( result.status != 200) {
      return this.setState({ error: result.statusText })
    }
    this.setState({ justSaved: true });
  };

  async load(event) {
    console.log('load: ', event);
  }

  
  render() {
    if (this.state.justSaved) return <div>Saved</div>;

    const loadReports = this.state.reports.map(report => <button onClick={(event) => load(report.id)}>{ report.project} - { report.id }</button>)

    return (
      <React.Fragment>
        <div>{ loadReports || ''}</div>
        <p>{ this.state.error }</p>
        <JSONSchemaForm
          schema={this.state.schema}
          onSubmit={(event) => this.submit(event)}
        />
      </React.Fragment>
    );
  }
}

export default App;



